/* item.css */

/* Example transition classes for fading in and out */
.carousel-item-enter {
  opacity: 0;
}

.carousel-item-enter-active {
  opacity: 1;
  transition: opacity 300ms;
}

.carousel-item-exit {
  opacity: 1;
}

.carousel-item-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}
.noneTextdecoration {
  text-decoration: none !important;
  color:black;
}

.smaller-image {
  width: 200px; /* Set the desired width here */
  /* You can also set a max-width or max-height if needed */
}