.home-container {
  position: relative;
  width: 100%;
  min-width: 100%;
  margin-right: 0;
  margin-top:-10px;
  margin-left: 0;
  height: 1000px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top center;
  background-attachment: fixed;
  background:url("../../../public/images/home.jpg")
}



.fade-in-out {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  animation: fade-in-out 15s infinite; /* Adjust duration as needed */
  text-shadow: 2px 2px 4px #000000; /* Add black shadow */
}

@keyframes fade-in-out {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

h1 {
  color: white;
}
