.signup-form-container {
    max-width: 300px;
    margin: auto;
    margin-top: 25px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  label {
    display: block;
    margin-bottom: 8px;
  }
  
  input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
  }
  
  button {
    background-color: #28a745;
    color: #fff;
    padding: 10px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #218838;
  }

  .success{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 500px;
  }
  .signupPage{
    height: 600px;
  }