.slick-dots li button {
  position: relative;
  display: block;
  width: 100%;
  height: 12px !important;
  padding: 0;
  margin-left: 5px !important;
  margin-right: 5px !important;
  color: transparent;
  font-size: 0;
  background: #ffffff;
  border: 2px solid #dcdcdc !important;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  opacity: 0.3;
  transition: all 0.3s;
  bottom: 0;
  margin-top: 100px !important; 
}

.ant-image-preview-close {
  position: fixed;
  top: 32px;
  right: 32px;
  margin-right:50px;
  display: flex;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  padding: 12px;
  outline: 0;
  border: 0;
  cursor: pointer;
  transition: all 0.3s;
}