header {
  /* background-image: url('../../../public/images/header.jpg'); */
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: #fff;
  padding: 10px;
}

header button {
  margin: 0 10px;
}

.cart {
  position: relative;
  display: block;
  width: 35px;
  height: 35px;
  height: auto;
  overflow: hidden;
}

.material-icons {
  position: relative;
  top: 4px;
  z-index: 1;
  font-size: 24px;
  color: white;
}
.cartDescription{
  margin-left: 45px;
}
