/* Define styles for the main container */
.main {
    padding: 0;
    margin: 0;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin-left:0;
    height: auto;
    width: 100%;
    margin-bottom: 100px;
}
ul {
    display: block;
    list-style-type: none;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
}
/* Define styles for each item */
.item {
    width: 30%; /* Initial width for larger screens */
    min-width: 130px; /* Minimum width for each item */
    height: 200px;
    margin-top: 30px;
    margin: 1%;
    float: left;
    margin-bottom: 200px;
    padding: 20px;
    box-sizing: border-box;
    list-style: none;
}
@media (max-width: 2000px) {
    .item {
        padding: 5px;
        padding-left: 70px;
        padding-right: 70px;
    }
}
@media (max-width: 1700px) {
    .item {
        padding: 5px;
        padding-left: 50px;
        padding-right: 50px;
    }
}
@media (max-width: 1500px) {
    .item {
        padding: 5px;
        padding-left: 30px;
        padding-right: 30px;
    }
}
@media (max-width: 1200px) {
    .item {
        padding: 10px;
        padding-left: 0px;
        padding-right: 0px;
    }
    ul {
        padding-inline-start: 0;
    }
}@media (max-width: 1100px) {
    .item {
        padding: 5px;
    }
}
@media (max-width: 1000px) {
    .item {
        padding: 0px;
    }
}
@media (max-width: 900px) {
    .item {
        margin-top: 50px;
    }
}
@media (max-width: 830px) {
    .main {
        margin-top: 30px;
    }
}
@media (max-width: 823px) {
    .item {
        margin-top: 20px;
        margin-left: 10px;
        margin-right: 10px;
    }
}


@media (max-width: 700px) {
    .item {
        margin-right:5px; /* Two items per row on smaller screens */
        margin-left:5px;
    }
}
@media (max-width: 680px) {
    .item {
        margin-right:5px; /* Two items per row on smaller screens */
        margin-left:5px;
        width: 30%; /* Full width for smaller screens */
    }
}
/* Media query for screens with a maximum width of 600px */
@media (max-width: 600px) {
    .item {
        margin-right:20px; /* Two items per row on smaller screens */
        margin-left:25px;
        width: 40%; /* Full width for smaller screens */
    }
}
@media (max-width: 550px) {
    .item {
        margin-right:10px; /* Two items per row on smaller screens */
        margin-left:15px;
        width: 40%; /* Full width for smaller screens */
    }
}