/* cart.css */
.overflow{
    overflow: auto;
    height: 400px;
    background-color: rgb(240, 240, 240);
    margin:10px;
    border: 1px solid #a8a8a8;
}
:where(.css-1ae8k9u).ant-drawer .ant-drawer-body {
  flex: 1;
  min-width: 0;
  min-height: 0;
  padding: 0px;
  overflow: auto;
}
.cart-item {
 /* Remove default list item decoration */
    display: flex;
    border: 1px solid #ccc;
    margin: 0px;
    padding: 10px;
    width: 100%;
  }
  
  .cart-item img {
    width: 100px; /* Adjust the width as needed */
    height: auto;
    margin-right: 10px;
  }
  
  .cart-item div {
    flex-grow: 1;
  }
  
  .cart-item h3 {
    margin-bottom: 5px;
  }
  
  .cart-item p {
    margin: 0;
  }
  
  .cart-item button {
    align-self: flex-end; /* Align button to the flex-end (bottom) of the container */
  }